@import './app-colors.scss';

$min-font-size: 18px;

$global-width: 1200px;

@mixin breakpoint($point) {
  @if $point == large {
    @media (min-width: 1201px) { @content ; }
  }
  @else if $point == medium {
    @media (min-width: 721px) and (max-width: 1200px) { @content ; }
  }
  @else if $point == small {
    @media (max-width: 720px) { @content ; }
  }
}